<template>
<!-- 普通分类部分 -->
   <div class="normal">
       
        <van-row gutter="10" >
        <van-col span="12" @click="sort(item)" v-for="(item,index) in $store.state.books.sort_normal" :key="index" ><span>| {{item.sort}} </span><img :src="item.src" alt=""></van-col>
    </van-row>
   </div>
</template>
<script>
export default {
    methods:{
        sort(item){
            this.$store.commit('books/jump_sortpage',item.sort);
            this.$router.push({path:"/sort-page"});
        }
    }
}
</script>
<style lang="scss" scoped>
    .normal{
        width:100%;
    }
    .van-row{
        width:98%;
        margin:0 auto !important;
    }
    .van-col>img{
        width:100%;
        height:100%;
        display:block;
    }
    .van-col>span{
        color: #535252;
        font-size: 0.7rem;
        position: relative;
        left: 0.9rem;
        top: 3.05rem;
    }
    
</style>